(function() {
  'use strict';

  angular
    .module('app')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creation" is avaible by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('app')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('app')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('main.factory', [])
    .factory('MainFactory', MainFactory);

  /** @ngInject */
  function MainFactory($http) {
    var _factory = function (data) {
      angular.extend(this, data);
    };

    _factory.getBrands = function(){
      return $http.get('assets/json/brands.json')
        .then(function(response){
          return response.data;
        });
    };

    _factory.getInteractions = function(){
      return $http.get('assets/json/interactions.json')
        .then(function(response){
          return response.data;
        });
    };

    _factory.getUsers = function(){
      return $http.get('assets/json/users.json')
        .then(function(response){
          return response.data;
        });
    };

    return _factory;
  }
})();

(function() {
  'use strict';

  angular
    .module('app')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, $scope, $filter, webDevTec, toastr, MainFactory) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1465947483658;
    vm.showToastr = showToastr;

    activate();
    populateData();

    vm.brands = [];
    $scope.selectedBrands = [];
    vm.interactions = [];
    vm.filteredInteractions = [];
    vm.chartData = [];
    vm.users = [];

    vm.barChart = {};
    vm.barChart.type = "BarChart";
    vm.barChart.data = {"cols": [], "rows": []};

    vm.translation = {
      selectAll       : "Selecionar Todos",
      selectNone      : "Selecionar Nenhum",
      reset           : "Reset",
      search          : "Buscar por...",
      nothingSelected : "Nenhum selecionado"
    };

    vm.onBrandSelected = function(){
      if ($scope.selectedBrands.length > 0){
        vm.filteredInteractions = _.filter(vm.interactions, function (interaction) {
          return _.find($scope.selectedBrands, {'id': interaction.brand});
        });
      }
      else{
        vm.filteredInteractions = [];
      }

      vm.populateChartData();
    };

    vm.populateChartData = function () {
      var data = _.groupBy(vm.filteredInteractions, 'user');
      var rows = [];

      angular.forEach(vm.users, function (user, iUser) {
        user.interactions = (data[user.id] ? data[user.id] : []);
      });
      var orderedUsers = $filter('limitTo')($filter('orderBy')(vm.users, 'interactions.length', true), 5);

      angular.forEach(orderedUsers, function (user, iUser) {
        rows.push({c: [
          {v: _.capitalize(user.name.title) + ' ' +  _.capitalize(user.name.first) + ' ' +  _.capitalize(user.name.last)},
          {v: user.interactions.length}
        ]});
      });

      vm.barChart.data = {"cols": [
        {id: "n", label: "Nome Usuário", type: "string"},
        {id: "i", label: "Interações", type: "number"}
      ], "rows": rows};
    };

    $scope.$watch('selectedBrands', function () {
      vm.onBrandSelected();
    });

    function populateData(){
      MainFactory.getBrands()
        .then(function (response) {
          vm.brands = response;

          angular.forEach(vm.brands, function (brand, iBrand) {
            brand.icon = '<img src="' + brand.image + '"/>';
          });
        });

      MainFactory.getInteractions()
        .then(function (response) {
          vm.interactions = response;
          vm.filteredInteractions = vm.interactions;

          vm.onBrandSelected();
        });
      MainFactory.getUsers()
        .then(function (response) {
          vm.users = response;
        });
    };

    function activate() {
      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    }

    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
  }
})();

